.intro {
  text-align: center;
  margin: 25px 0 0; }
  .intro .h2 {
    margin-bottom: 1.2em; }
  .intro .donation-value {
    font-size: 28px;
    margin: 0;
    color: red;
    font-weight: bold; }
  .intro .donation-subhead {
    margin: 0;
    font-size: 13px; }
  .intro .introduction {
    margin: 0 auto 25px;
    padding: 30px 35px 0;
    max-width: 660px; }
