.content {
  margin: 26px 0px 62px; }

.content .inner {
  margin: 0 auto;
  max-width: 660px;
  padding: 0px 25px; }

.content.dark {
  background-color: #12092d;
  min-height: 300px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .content.dark p {
    color: white; }
  .content.dark .inner {
    padding: 22px 42px; }
