/*
Breakpoints
-----------

Use it like this:

.selector {
  @include bp(tablet) {
    color: $blue-light;
  }
}
*/
@import url("https://fonts.googleapis.com/css?family=Lato:400,400i,700,700i");
h1,
h2,
h3,
h4,
h5,
h6,
p,
li,
input,
button,
a,
.react-select-container {
  font-family: 'Lato', sans-serif; }

h2 {
  font-weight: bold;
  font-size: 20px;
  color: #12092d; }

h3 {
  line-height: 1.4em;
  color: #12092d; }

p,
li {
  font-size: 16px;
  color: #4d4d4d;
  line-height: 1.5em; }

a {
  color: #228ae6; }
  a:hover {
    color: #1670bf; }

.slick-slider {
  margin-bottom: 50px; }
  .slick-slider .slick-slide {
    outline: 0; }
    .slick-slider .slick-slide div {
      outline: 0; }
  .slick-slider .slick-arrow {
    position: absolute;
    z-index: 100;
    top: calc(50% - 31px);
    right: 12px;
    background-color: transparent;
    background-image: url(../../images/slider-next.svg);
    background-size: 62px 62px;
    font-size: 0;
    line-height: 0;
    color: transparent;
    width: 62px;
    height: 62px;
    border: 0;
    outline: 0;
    cursor: pointer; }
    .slick-slider .slick-arrow:hover {
      background-image: url(../../images/slider-next-hover.svg); }
    .slick-slider .slick-arrow.slick-prev {
      transform: rotate(180deg);
      right: auto;
      left: 12px; }

#arrow-preloader {
  background: url(../../images/slider-next-hover.svg) no-repeat -9999px -9999px; }

.bw-slide {
  background: white;
  box-shadow: 0 0 16px 4px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  margin: 16px 10px;
  height: 530px;
  overflow: hidden; }
  @media (min-width: 768px) {
    .bw-slide {
      height: 480px; } }
  @media (min-width: 1000px) {
    .bw-slide {
      height: 625px; } }
  .bw-slide__image {
    width: 100%;
    height: 116.6px;
    background-size: cover;
    background-position: center; }
    @media (min-width: 768px) {
      .bw-slide__image {
        height: 168px; } }
    @media (min-width: 1000px) {
      .bw-slide__image {
        height: 218.75px; } }
  .bw-slide__content {
    margin: 15px;
    height: calc(100% - 146.6px); }
    .bw-slide__content--no-image {
      margin: 15px;
      height: calc(100% - 30px); }
    @media (min-width: 768px) {
      .bw-slide__content {
        height: calc(100% - 198px); } }
    @media (min-width: 1000px) {
      .bw-slide__content {
        margin: 20px;
        height: calc(100% - 258.75px); }
        .bw-slide__content--no-image {
          margin: 20px;
          height: calc(100% - 40px); } }
  .bw-slide__scrollable {
    height: calc(100% - 30px);
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch; }
    .bw-slide__scrollable p:first-child {
      margin-top: 0; }
    .bw-slide__scrollable p:last-child {
      margin-bottom: 0; }
  .bw-slide__name {
    padding-bottom: 14px;
    line-height: 24px; }
    @media (min-width: 768px) {
      .bw-slide__name {
        line-height: 28px; } }
  .bw-slide__donations {
    display: flex;
    justify-content: space-between;
    margin-top: 5px; }
  .bw-slide__donation {
    font-size: 14px;
    font-weight: bold;
    text-align: left;
    line-height: 1.2em;
    color: #12092d; }
    .bw-slide__donation:not(:first-child) {
      margin-left: 5px; }
  .bw-slide__donation-value {
    color: red; }
  .bw-slide h3 {
    font-size: 20px;
    color: #12092d;
    font-weight: bold;
    margin: 0; }
    @media (min-width: 768px) {
      .bw-slide h3 {
        font-size: 24px; } }
  .bw-slide h4 {
    font-size: 28px;
    color: #12092d;
    line-height: 34px;
    font-weight: bold;
    margin: 0; }
  .bw-slide p {
    font-size: 14px;
    line-height: 1.5em;
    color: #4d4d4d; }
    @media (min-width: 1200px) {
      .bw-slide p {
        font-size: 15px; } }
  .bw-slide.buffy-wicks {
    box-shadow: 0 0 16px 6px rgba(0, 0, 0, 0.35); }
    .bw-slide.buffy-wicks .bw-slide__name {
      display: block;
      text-align: center; }
    .bw-slide.buffy-wicks h3 {
      font-size: 30px;
      line-height: normal; }
      @media (min-width: 768px) {
        .bw-slide.buffy-wicks h3 {
          font-size: 35px; } }
  .bw-slide__categories {
    display: flex;
    flex-wrap: wrap;
    margin-top: 14px; }
  .bw-slide__category {
    font-family: 'Lato', sans-serif;
    background: #999999;
    border-radius: 4px;
    padding: 5px 8px;
    font-size: 13px;
    color: white;
    margin: 5px 5px 0 0; }

.filters-wrapper {
  margin: 10px 0 15px; }

.filter-wrapper {
  padding: 15px 20px 0;
  max-width: 500px;
  margin: 0 auto; }
