.social-links {
  display: flex;
  flex-direction: row;
  align-items: right;
  justify-content: flex-end;
  flex: 1 1 auto;
  margin: 0 20px;
  padding: 0; }

.social-links__link {
  flex: 0 0 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  margin: 0;
  transition: opacity 0.1s; }
  .social-links__link:hover {
    opacity: 0.8; }
  .social-links__link:focus {
    outline: none;
    box-shadow: 0 0 0 4px #7ebbf0;
    border-radius: 5px; }
  @media (min-width: 768px) {
    .social-links__link {
      justify-content: flex-start;
      margin: 0 10px; } }
